<template>
<div>
    <v-app-bar color="#003263" dark src="../assets/images/vbanner5.jpg">
        <template v-slot:img="{ props }">
            <v-img v-bind="props" gradient="to top right, rgb(1,48,98,.9), rgb(22,61,99,.8)"></v-img>
        </template>

        <div>
            <img src="../assets/images/ghub_white_.png" style="margin-top:50px;" width="150" class="bounceIn">
        </div>

        <v-spacer></v-spacer>
        <v-toolbar-title>Supplier Management</v-toolbar-title>

        <template v-slot:extension>
            <v-tabs right>
                <v-tab>Supplier List</v-tab>
            </v-tabs>
        </template>

    </v-app-bar>
    <div style="background-color:#EBEFF3;height:95vh;overflow-y:auto;">
        <br>
        <v-card class="ml-5 mr-5 mb-5">

            <v-data-table :headers="headers" :items="users" :search="search" sort-by="supplierId" class="elevation-1">
                <template v-slot:top>
                    <v-toolbar flat color="white">

                        <v-spacer></v-spacer>
                        <v-text-field dense v-model="search" append-icon="search" label="Search" solo class="mt-5 mr-5"></v-text-field>
                        <v-dialog v-model="dialog" max-width="500px">
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">New Supplier</v-btn>
                            </template>
                            <v-card>
                                <v-card-title class="headline blue lighten-2 white--text" primary-title>
                                    <span class="headline">{{ formTitle }}</span>
                                </v-card-title>

                                <v-card-text>
                                    <v-container>
                                        <v-row>

                                            <v-col cols="12" sm="12" md="12">
                                                <v-text-field v-model="editedUser.supplierName" label="Supplier Name"></v-text-field>
                                            </v-col>
                                            <v-col cols="12" sm="12" md="12">
                                                <v-text-field v-model="editedUser.supplierAddress" label="Supplier Address"></v-text-field>
                                            </v-col>
                                            <v-col cols="12" sm="12" md="12">
                                                <v-text-field v-model="editedUser.supplierEmail" label="Email Address"></v-text-field>
                                            </v-col>
                                            <v-col cols="12" sm="12" md="12">
                                                <v-text-field v-model="editedUser.supplierPhone" label="Supplier Phone"></v-text-field>
                                            </v-col>
                                            <v-col cols="12" sm="12" md="12">
                                                <v-text-field v-model="editedUser.VATNumber" label="BP Number"></v-text-field>
                                            </v-col>

                                        </v-row>
                                    </v-container>
                                </v-card-text>

                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                                    <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>
                    </v-toolbar>
                </template>

                <template v-slot:item.actions="{ item }">

                    <v-tooltip bottom v-if="userTypeAdmin == 1">
                        <template v-slot:activator="{ on, attrs }">
                            <v-icon v-bind="attrs" v-on="on" color="blue" class="mr-2" @click="editItem(item)">
                                edit
                            </v-icon>
                        </template>
                        <span>Edit Supplier</span>
                    </v-tooltip>

                    <v-tooltip bottom v-if="item.status != 1">
                        <template v-slot:activator="{ on, attrs }">
                            <v-icon v-bind="attrs" v-on="on" color="red" class="mr-2" @click="changeStatus(item,1)">
                                face
                            </v-icon>
                        </template>
                        <span>Activate Supplier</span>
                    </v-tooltip>

                    <v-tooltip bottom v-if="item.status != 0">
                        <template v-slot:activator="{ on, attrs }">
                            <v-icon v-bind="attrs" v-on="on" color="green" class="mr-2" @click="changeStatus(item,0)">
                                face
                            </v-icon>
                        </template>
                        <span>De-Activate Supplier</span>
                    </v-tooltip>

                    <v-tooltip bottom v-if="item.status != 0">
                        <template v-slot:activator="{ on, attrs }">
                            <v-icon v-bind="attrs" v-on="on" color="red" class="mr-2" @click="deleteItem(item,0)">
                                delete
                            </v-icon>
                        </template>
                        <span>Delete Supplier</span>
                    </v-tooltip>

                </template>
                <template v-slot:no-data>
                    <h3>No Records Found</h3>
                </template>
                <template v-slot:item.status="{ item }">
                    {{userStatus(item)}}
                </template>

                <template v-slot:item.userTypes="{ item }">
                    {{userType(item)}}
                </template>
            </v-data-table>

        </v-card>
    </div>

    <v-dialog v-model="dialogDelete" max-width="500px">
        <v-card>
            <v-card-title class="headline">Are you sure you want to delete this item?</v-card-title>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
            </v-card-actions>
        </v-card>
    </v-dialog>

</div>
</template>

<script>
export default {
    data() {
        return {
            dialogDelete: false,
            nodeApi: '',
            page: 'Main Page',
            dialog: false,
            dialogRestPassword: false,
            search: '',
            confirm_password: '',
            userTypeData: [{
                id: 1,
                name: 'Super Admin'
            }, {
                id: 2,
                name: 'Admin'
            }, {
                id: 3,
                name: 'Accountant'
            }, {
                id: 4,
                name: 'Data Capturer'
            }],
            userStatusData: [{
                id: 1,
                name: 'Active'
            }, {
                id: 2,
                name: 'Inactive'
            }],
            headers: [{
                    text: 'Supplier ID',
                    value: 'supplierId'
                },
                {
                    text: 'Supplier Name',
                    value: 'supplierName'
                },
                {
                    text: 'Supplier Address',
                    value: 'supplierAddress'
                },
                {
                    text: 'Supplier Email',
                    value: 'supplierEmail'
                },
                {
                    text: 'Supplier Phone',
                    value: 'supplierPhone'
                },

                {
                    text: 'Supplier VAT No.',
                    value: 'VATNumber'
                },

                {
                    text: 'Actions',
                    value: 'actions',
                    sortable: false
                },
            ],
            users: [],
            editedIndex: -1,
            editedUser: {

                supplierName: '',
                supplierAddress: '',
                supplierEmail: '',
                supplierPhone: '',
                VATNumber: ''
            },
            defaultUser: {

                supplierName: '',
                supplierAddress: '',
                supplierEmail: '',
                supplierPhone: '',
                VATNumber: ''
            },
            passwordObj: {},
            userTypeAdmin: null,
            supplierId: ''
        }
    },
    computed: {
        formTitle() {
            return this.editedIndex === -1 ? 'New Supplier' : 'Edit Supplier'
        },
    },

    watch: {
        dialog(val) {
            val || this.close()
        },

    },
    mounted() {
        this.userTypeAdmin = this.$session.get('user').userTypes
        // console.log(this.$session.get('user').userType)
    },
    created() {
        this.initialize()
    },

    methods: {
        closeDelete() {
            this.dialogDelete = false

            this.$nextTick(() => {

                this.editedIndex = -1
            })
        },
        deleteItem(item) {
            // console.log(item)
            this.editedIndex = this.users.indexOf(item)
            this.supplierId = item.supplierId
            this.dialogDelete = true
        },

        deleteItemConfirm() {
            // console.log(this.supplierId)
            this.users.splice(this.editedIndex, 1)
            this.axios.post(this.nodeApi + '/removeSupplier', {
                supplierId: this.supplierId
            }).then((response) => {
                // console.log(response.data)
                if (response.data.affectedRows > 0) {
                    this.$swal.fire({
                        type: 'success',
                        title: 'Supplier Deleted successfully',
                        text: 'You have successfully added a new sales order'
                    })
                    this.closeDelete()

                } else {

                    this.$swal.fire({
                        type: 'error',
                        title: 'Supplier Deleting Failed',
                        text: response.data.originalError.info.message
                    })
                }

            }).catch(err => {

                this.$swal.fire({
                    type: 'error',
                    title: 'Item Deleting Failed',
                    text: err
                })
            })
        },
        initialize() {
            this.axios.get(this.nodeApi + '/getAllSuppliers').then((response) => {
                this.users = response.data
            }).catch(err => {
                console.log(err)
            })

        },
        userStatus(item) {
            if (item.status == 0) {
                return 'Inactive'
            } else {
                return 'Active'
            }
        },
        userType(item) {

            if (item.userTypes == 1) {
                console.log(item.userTypes)
                return 'Super Admin'
            } else if (item.userTypes == 2) {
                return 'Admin'
            } else if (item.userTypes == 3) {
                return 'Accountant'
            } else if (item.userTypes == 4) {
                return 'Data Capturer'
            }
        },

        changeStatus(item, status) {
            // console.log(item.status + '->' + status)
            let data = {

                supplierId: item.supplierId,
                status: status

            }
            let statusName = ''
            if (status == 1) {
                statusName = 'Activated'
            } else {
                statusName = 'De-Activated'
            }

            this.axios.post(this.nodeApi + '/updateSupplierStatus', data).then((response) => {

                if (response.data.affectedRows > 0) {
                    this.$swal.fire({
                        type: 'success',
                        title: 'User Updated',
                        text: 'Your supplier was successfully ' + statusName + '.'
                    })
                    this.initialize()
                } else {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Updating Supplier Failed',
                        text: response.data.originalError.info.message
                    })
                }

            }).catch(err => {
                this.$swal.fire({
                    type: 'error',
                    title: 'Updating Supplier Failed',
                    text: err
                })
            })
        },

        editItem(item) {
            this.editedIndex = this.users.indexOf(item)
            this.editedUser = Object.assign({}, item)
            this.dialog = true
        },

        close() {
            this.dialog = false
            this.confirm_password = ''
            this.$nextTick(() => {
                this.editedUser = Object.assign({}, this.defaultUser)
                this.editedIndex = -1
            })

        },

        save() {

            if (this.editedIndex > -1) {

                let data = {

                    supplierName: this.editedUser.supplierName,
                    supplierAddress: this.editedUser.supplierAddress,
                    supplierEmail: this.editedUser.supplierEmail,
                    supplierPhone: this.editedUser.supplierPhone,
                    supplierId: this.editedUser.supplierId,
                    VATNumber: this.editedUser.VATNumber,
                    createdBy: this.$session.get('user').userId

                }

                this.axios.post(this.nodeApi + '/updateSupplier', data).then((response) => {

                    if (response.data.affectedRows > 0) {
                        this.$swal.fire({
                            type: 'success',
                            title: 'Customer Updated',
                            text: 'Your supplier was successfully updated.'
                        })
                        this.initialize()
                    } else {
                        this.$swal.fire({
                            type: 'error',
                            title: 'Updating Supplier Failed',
                            text: response.data.originalError.info.message
                        })
                    }

                }).catch(err => {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Updating Supplier Failed',
                        text: err
                    })
                })
                this.close()
            } else {
                if (!this.editedUser.supplierName) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding Supplier Failed',
                        text: 'Please enter a valid Supplier Name'
                    })
                } else if (!this.editedUser.supplierAddress) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding Supplier Failed',
                        text: 'Please enter a valid Supplier Address'
                    })
                } else if (!this.editedUser.supplierEmail) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding Supplier Failed',
                        text: 'Please enter a valid Email Address'
                    })
                } else if (!this.editedUser.supplierPhone) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding Supplier Failed',
                        text: 'Please select a valid Supplier Phone'
                    })
                } else if (!this.editedUser.VATNumber) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding Supplier Failed',
                        text: 'Please enter a valid VAT Number'
                    })
                } else {

                    this.editedUser.createdBy = this.$session.get('user').userId
                    // console.log(this.editedUser)
                    this.axios.post(this.nodeApi + '/addSupplier', this.editedUser).then((response) => {
                        // console.log(response.data)
                        if (response.data.affectedRows > 0) {
                            this.$swal.fire({
                                type: 'success',
                                title: 'User Supplier',
                                text: 'Your new Supplier was successfully added.'
                            })
                            this.initialize()
                        } else {
                            this.$swal.fire({
                                type: 'error',
                                title: 'Adding Supplier Failed',
                                text: response.data.originalError.info.message
                            })
                        }

                    }).catch(err => {
                        this.$swal.fire({
                            type: 'error',
                            title: 'Adding Supplier Failed',
                            text: err
                        })
                    })
                    this.close()
                }
            }

        },
    },

}
</script>

<style scoped>

</style>
