<template>
<div>
    <v-app-bar color="#003263" dark src="../assets/images/vbanner5.jpg">
        <template v-slot:img="{ props }">
            <v-img v-bind="props" gradient="to top right, rgb(1,48,98,.9), rgb(22,61,99,.8)"></v-img>
        </template>

        <div>
            <img src="../assets/images/ghub_white_.png" style="margin-top:50px;" width="150" class="bounceIn">
        </div>

        <v-spacer></v-spacer>
        <v-toolbar-title>User Management</v-toolbar-title>

        <template v-slot:extension>
            <v-tabs right>
                <v-tab>Users List</v-tab>
            </v-tabs>
        </template>

    </v-app-bar>
    <div style="background-color:#EBEFF3;height:95vh;overflow-y:auto;">
        <br>
        <v-card class="ml-5 mr-5 mb-5">

            <v-data-table :headers="headers" :items="users" :search="search" sort-by="userId" class="elevation-1">
                <template v-slot:top>
                    <v-toolbar flat color="white">
                        <!-- <v-toolbar-title >User Administration</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider> -->
                        <v-spacer></v-spacer>
                        <v-text-field dense v-model="search" append-icon="search" label="Search" solo class="mt-5 mr-5"></v-text-field>
                        <v-dialog v-model="dialog" max-width="500px">
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">New User</v-btn>
                            </template>
                            <v-card>
                                <v-card-title class="headline blue lighten-2 white--text" primary-title>
                                    <span class="headline">{{ formTitle }}</span>
                                </v-card-title>

                                <v-card-text>
                                    <v-container>
                                        <v-row>

                                            <v-col cols="12" sm="6" md="6">
                                                <v-text-field v-model="editedUser.firstName" label="First Name"></v-text-field>
                                            </v-col>
                                            <v-col cols="12" sm="6" md="6">
                                                <v-text-field v-model="editedUser.lastName" label="Last Name"></v-text-field>
                                            </v-col>
                                            <v-col cols="12" sm="12" md="12">
                                                <v-text-field v-model="editedUser.emailAddress" label="Email Address"></v-text-field>
                                            </v-col>
                                            <v-col cols="12" sm="12" md="12">
                                                <v-select @input="getUserType" v-model="editedUser.usertype" item-text="name" item-value="id" class="text-left" :items="userTypeData" label="Select UserType"></v-select>
                                            </v-col>

                                            <v-col cols="12" sm="6" md="6" v-if="formTitle == 'New User'">
                                                <v-text-field v-model="editedUser.userPassword" type="password" label="Password"></v-text-field>
                                            </v-col>
                                            <v-col cols="12" sm="6" md="6" v-if="formTitle == 'New User'">
                                                <v-text-field v-model="confirm_password" type="password" label="Confirm Password"></v-text-field>
                                            </v-col>
                                            <!-- <v-col cols="12" sm="6" md="6" v-if="editedUser.usertype > 3">
                                                <v-text-field v-model="editedUser.nationalId" type="text" label="National ID"></v-text-field>
                                            </v-col>
                                            <v-col cols="12" sm="6" md="6" v-if="editedUser.usertype > 3">
                                                <v-text-field v-model="editedUser.driversId" type="text" label="Drivers Licence No."></v-text-field>
                                            </v-col> -->

                                        </v-row>
                                    </v-container>
                                </v-card-text>

                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                                    <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>
                    </v-toolbar>
                </template>

                <template v-slot:item.actions="{ item }">

                    <v-tooltip bottom v-if="userTypeAdmin == 1">
                        <template v-slot:activator="{ on, attrs }">
                            <v-icon v-bind="attrs" v-on="on" color="blue" class="mr-2" @click="editItem(item)">
                                edit
                            </v-icon>
                        </template>
                        <span>Edit User</span>
                    </v-tooltip>

                    <v-tooltip bottom v-if="item.status != 1">
                        <template v-slot:activator="{ on, attrs }">
                            <v-icon v-bind="attrs" v-on="on" color="red" class="mr-2" @click="changeStatus(item,1)">
                                face
                            </v-icon>
                        </template>
                        <span>Activate User</span>
                    </v-tooltip>

                    <v-tooltip bottom v-if="item.status != 0">
                        <template v-slot:activator="{ on, attrs }">
                            <v-icon v-bind="attrs" v-on="on" color="green" class="mr-2" @click="changeStatus(item,0)">
                                face
                            </v-icon>
                        </template>
                        <span>De-Activate User</span>
                    </v-tooltip>

                    <v-tooltip bottom v-if="userTypeAdmin == 1">
                        <template v-slot:activator="{ on, attrs }">
                            <v-icon v-bind="attrs" v-on="on" color="gray" class="mr-2" @click="openRestPassword(item)">
                                vpn_key
                            </v-icon>
                        </template>
                        <span>Reset Password</span>
                    </v-tooltip>

                    <v-tooltip bottom v-if="userTypeAdmin == 1">
                        <template v-slot:activator="{ on, attrs }">
                            <v-icon v-bind="attrs" v-on="on" color="red" class="mr-2" @click="deleteItem(item)">
                                delete
                            </v-icon>
                        </template>
                        <span>Delete User</span>
                    </v-tooltip>

                </template>
                <template v-slot:no-data>
                    <v-btn color="primary" @click="initialize">Reset</v-btn>
                </template>
                <template v-slot:item.status="{ item }">
                    {{userStatus(item)}}
                </template>

                <template v-slot:item.userTypes="{ item }">
                    {{userType(item)}}
                </template>
            </v-data-table>

        </v-card>
    </div>
    <v-dialog v-model="dialogRestPassword" max-width="500px">
        <v-card>
            <v-card-title class="headline red lighten-2 white--text" primary-title>
                <span class="headline">Reset Password</span>
            </v-card-title>

            <v-card-text>

                <v-row>
                    <v-col cols="12" sm="6" md="6">
                        <v-text-field v-model="editedUser.userPassword" type="password" label="Password"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="6">
                        <v-text-field v-model="confirm_password" type="password" label="Confirm Password"></v-text-field>
                    </v-col>

                </v-row>

            </v-card-text>

            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="dialogRestPassword = false">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="restPassword">Save</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>

    <v-dialog v-model="dialogDelete" max-width="500px">
        <v-card>
            <v-card-title class="headline">Are you sure you want to delete this item?</v-card-title>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
            </v-card-actions>
        </v-card>
    </v-dialog>

</div>
</template>

<script>
export default {
    data() {
        return {
            dialogDelete: false,
            nodeApi: '',
            page: 'Main Page',
            dialog: false,
            dialogRestPassword: false,
            search: '',
            confirm_password: '',
            userTypeData: [{
                id: 1,
                name: 'Super Admin'
            }, {
                id: 2,
                name: 'Sales Officer'
            }, {
                id: 3,
                name: 'Accountant'
            }, {
                id: 4,
                name: 'Driver'
            }, {
                id: 5,
                name: 'Silo Officer'
            }],
            userStatusData: [{
                id: 1,
                name: 'Active'
            }, {
                id: 2,
                name: 'Inactive'
            }],
            headers: [{
                    text: 'User ID',
                    value: 'userId'
                },
                {
                    text: 'First Name',
                    value: 'firstName'
                },
                {
                    text: 'Last Name',
                    value: 'lastName'
                },
                {
                    text: 'Email Address',
                    value: 'emailAddress'
                },
                {
                    text: 'User Status',
                    value: 'status'
                },
                {
                    text: 'User Type',
                    value: 'userTypes'
                },
                {
                    text: 'Actions',
                    value: 'actions',
                    sortable: false
                },
            ],
            users: [],
            editedIndex: -1,
            editedUser: {

                firstName: '',
                lastName: '',
                emailAddress: '',
                userPassword: '',
                userTypes: '',
                nationalId: '',
                driversId: '',
                usetype: ''
            },
            defaultUser: {

                firstName: '',
                lastName: '',
                emailAddress: '',
                userPassword: '',
                userTypes: '',
                nationalId: '',
                driversId: '',
                usetype: ''
            },
            passwordObj: {},
            userTypeAdmin: null,
            userId: ''
        }
    },
    computed: {
        formTitle() {
            return this.editedIndex === -1 ? 'New User' : 'Edit User'
        },
    },

    watch: {
        dialog(val) {
            val || this.close()
        },

    },
    mounted() {
        this.userTypeAdmin = this.$session.get('user').userTypes
        // console.log(this.userTypeAdmin)
    },
    created() {
        this.initialize()
    },

    methods: {
        closeDelete() {
            this.dialogDelete = false

            this.$nextTick(() => {

                this.editedIndex = -1
            })
        },
        deleteItem(item) {
            // console.log(item)
            this.editedIndex = this.users.indexOf(item)
            this.userId = item.userId
            this.dialogDelete = true
        },

        deleteItemConfirm() {
            // console.log(this.userId)
            this.users.splice(this.editedIndex, 1)
            this.axios.post(this.nodeApi + '/removeUser', {
                userId: this.userId
            }).then((response) => {
                // console.log(response.data)
                if (response.data.affectedRows > 0) {
                    this.$swal.fire({
                        type: 'success',
                        title: 'User Deleted successfully',
                        text: 'You have successfully added a new sales order'
                    })
                    this.closeDelete()

                } else {

                    this.$swal.fire({
                        type: 'error',
                        title: 'User Deleting Failed',
                        text: response.data.originalError.info.message
                    })
                }

            }).catch(err => {

                this.$swal.fire({
                    type: 'error',
                    title: 'Item Deleting Failed',
                    text: err
                })
            })
        },
        getUserType() {
            console.log(typeof (this.editedUser.usertype))

        },
        initialize() {
            this.axios.get(this.nodeApi + '/getAllUsers').then((response) => {
                this.users = response.data
            }).catch(err => {
                console.log(err)
            })

        },
        userStatus(item) {
            if (item.status == 0) {
                return 'Inactive'
            } else {
                return 'Active'
            }
        },
        userType(item) {

            if (item.userTypes == 1) {
                // console.log(item.userTypes)
                return 'Super Admin'
            } else if (item.userTypes == 2) {
                return 'Sales Officer'
            } else if (item.userTypes == 3) {
                return 'Accountant'
            } else if (item.userTypes == 4) {
                return 'Truck Driver'
            }else if (item.userTypes == 5) {
                return 'Silo Officer'
            }
        },
        openRestPassword(item) {
            this.dialogRestPassword = true
            this.passwordObj = {
                userId: item.userId,
                userPassword: ''

            }
        },
        restPassword() {
            this.passwordObj.userPassword = this.confirm_password
            if (!this.editedUser.userPassword) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Update User Failed',
                    text: 'Please enter a valid Password'
                })
            } else if (this.editedUser.userPassword != this.confirm_password) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Update User Failed',
                    text: 'Passwords do not match'
                })
            } else {

                this.axios.post(this.nodeApi + '/updatePassword', this.passwordObj).then((response) => {

                    if (response.data.affectedRows > 0) {
                        this.$swal.fire({
                            type: 'success',
                            title: 'User Updated',
                            text: 'Your user password was updated successfully.'
                        })
                        this.initialize()
                        this.dialogRestPassword = false
                        this.confirm_password = ''
                        this.editedUser.userPassword = ''
                    } else {
                        this.$swal.fire({
                            type: 'error',
                            title: 'Updating User Failed',
                            text: response.data.originalError.info.message
                        })
                    }

                }).catch(err => {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Updating User Failed',
                        text: err
                    })
                })
            }
        },
        changeStatus(item, status) {
            // console.log(item.status + '->' + status)
            let data = {

                userId: item.userId,
                status: status

            }
            let statusName = ''
            if (status == 1) {
                statusName = 'Activated'
            } else {
                statusName = 'De-Activated'
            }

            this.axios.post(this.nodeApi + '/updateStatus', data).then((response) => {

                if (response.data.affectedRows > 0) {
                    this.$swal.fire({
                        type: 'success',
                        title: 'User Updated',
                        text: 'Your user was successfully ' + statusName + '.'
                    })
                    this.initialize()
                } else {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Updating User Failed',
                        text: response.data.originalError.info.message
                    })
                }

            }).catch(err => {
                this.$swal.fire({
                    type: 'error',
                    title: 'Updating User Failed',
                    text: err
                })
            })
        },

        editItem(item) {
            this.editedIndex = this.users.indexOf(item)
            this.editedUser = Object.assign({}, item)
            this.dialog = true
        },

        close() {
            this.dialog = false
            this.confirm_password = ''
            this.$nextTick(() => {
                this.editedUser = Object.assign({}, this.defaultUser)
                this.editedIndex = -1
            })

        },

        save() {
            // if (this.editedUser.driversId) {
            //     this.editedUser.userPassword = '123456789'
            //     this.confirm_password = '123456789'
            // }
            // console.log(this.editedUser)
            if (this.editedIndex > -1) {

                let data = {

                    firstName: this.editedUser.firstName,
                    lastName: this.editedUser.lastName,
                    emailAddress: this.editedUser.emailAddress,
                    userId: this.editedUser.userId,
                    usertype: this.editedUser.usertype,
                    // nationalId: this.editedUser.nationalId,
                    // driversId: this.editedUser.driversId

                }

                this.axios.post(this.nodeApi + '/updateUser', data).then((response) => {

                    if (response.data.affectedRows > 0) {
                        this.$swal.fire({
                            type: 'success',
                            title: 'User Updated',
                            text: 'Your user was successfully updated.'
                        })
                        this.initialize()
                    } else {
                        this.$swal.fire({
                            type: 'error',
                            title: 'Updating User Failed',
                            text: response.data.originalError.info.message
                        })
                    }

                }).catch(err => {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Updating User Failed',
                        text: err
                    })
                })
                this.close()
            } else {
                if (!this.editedUser.firstName) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding User Failed',
                        text: 'Please enter a valid First Name'
                    })
                } else if (!this.editedUser.lastName) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding User Failed',
                        text: 'Please enter a valid Last Name'
                    })
                } else if (!this.editedUser.emailAddress) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding User Failed',
                        text: 'Please enter a valid Email Address'
                    })
                } else if (!this.editedUser.usertype) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding User Failed',
                        text: 'Please select a valid User Type'
                    })
                } else if (!this.editedUser.userPassword) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding User Failed',
                        text: 'Please enter a valid Password'
                    })
                } else if (this.editedUser.userPassword != this.confirm_password) {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding User Failed',
                        text: 'Passwords do not match'
                    })
                } else {

                    this.editedUser.createdBy = this.$session.get('user').userId
                    // console.log(this.editedUser)
                    this.axios.post(this.nodeApi + '/addUser', this.editedUser).then((response) => {
                        // console.log(response.data)
                        if (response.data.affectedRows > 0) {
                            this.$swal.fire({
                                type: 'success',
                                title: 'User Added',
                                text: 'Your new user was successfully added.'
                            })
                            this.initialize()
                            this.close()
                        } else {
                            this.$swal.fire({
                                type: 'error',
                                title: 'Adding User Failed',
                                text: response.data.originalError.info.message
                            })
                        }

                    }).catch(err => {
                        this.$swal.fire({
                            type: 'error',
                            title: 'Adding User Failed',
                            text: err
                        })
                    })

                }
            }

        },
    },

}
</script>

<style scoped>

</style>
