<template>
<div>
    <v-card color="white" hover width="230">
        <v-img class="white--text" height="100px" :src="imageUrL">
            <v-layout row>
                <v-layout column wrap>
                    <v-flex ma-4>

                    </v-flex>
                </v-layout>
                <v-layout column wrap justify-end>
                    <v-flex ma-4>
                        <div v-for="(stack, index) in value" :key="index">
                            <h2 class="iCountUp" style="text-align: right;margin-right:15px;margin-top:-10px;" v-if="name != 'Total Sales'">
                                {{stack.currencyISO}}
                                <ICountUp :delay="delay" :endVal="stack.income" :options="options" />
                            </h2>
                            <h2 class="iCountUp" style="text-align: right;margin-right:15px;margin-top:-10px;" v-if="name == 'Total Sales'">
                                {{stack.currencyISO}} Invoices
                                <ICountUp :delay="delay" :endVal="stack.income" :options="options2" />
                            </h2>
                        </div>

                    </v-flex>
                </v-layout>
            </v-layout>
        </v-img>
        <v-divider light></v-divider>
        <v-card-actions>

            <v-spacer></v-spacer>
            <h2>{{cardName}}</h2>
        </v-card-actions>
    </v-card>
</div>
</template>

<script>
//import Axios from 'axios-observable';
import ICountUp from 'vue-countup-v2';
export default {
    components: {
        ICountUp
    },
    props: ['value', 'name'],
    data() {
        return {
            imageUrL: '',
            delay: 200,
            cardName: '',
            stack_value: 0,
            options: {
                useEasing: true,
                useGrouping: true,
                separator: ',',
                decimal: '.',
                decimalPlaces: 2,

            },
            options2: {
                useEasing: true,
                useGrouping: true,
                separator: ',',
                decimal: '.',
                decimalPlaces: 0,

            },

        }

    },
    mounted() {

        if (this.name) {
            //  console.log(this.name)
            this.stack_value = this.value
            this.cardName = this.name
            if (this.cardName == 'Total Sales') {
                this.imageUrL = 'images/bg2.jpg'
            }
            if (this.cardName == 'Total Deliveries') {
                this.imageUrL = 'images/bg4.jpg'
            }
            if (this.cardName == 'Pending Deliveries') {
                this.imageUrL = 'images/bg3.jpg'
            }
            if (this.cardName == 'Total Income') {
                this.imageUrL = 'images/bg5.jpg'
            }
            if (this.cardName == 'Expenses') {
                this.imageUrL = 'images/bg1.jpg'
            }
            if (this.cardName == 'Net Profit') {
                this.imageUrL = 'images/bg2.jpg'
            }

        }
    },

    methods: {

        onReadyOut: function (instance) {
            const that = this;
            instance.update(that.stack_value);
        }
    }
}
</script>

<style scoped>
.iCountUp {
    /* font-size: 12em; */
    margin: 0;
    color: #ffffff;
    text-shadow: 2px 2px #333333;
}

.emojiGreen {
    color: transparent;
    text-shadow: 0 0 0 rgb(57, 255, 8);
}
</style>
