<template>
<div>
    <v-app-bar color="#003263" dark src="../assets/images/vbanner5.jpg">
        <template v-slot:img="{ props }">
            <v-img v-bind="props" gradient="to top right, rgb(1,48,98,.9), rgb(22,61,99,.8)"></v-img>
        </template>

        <div>
            <img src="../assets/images/ghub_white_.png" style="margin-top:50px;" width="150" class="bounceIn">
        </div>

        <v-spacer></v-spacer>
        <v-toolbar-title>Stock Management</v-toolbar-title>

        <template v-slot:extension>

            <v-tabs right v-model="tab">

                <v-tab v-for="item in items" :key="item">
                    {{ item }}
                </v-tab>
            </v-tabs>
        </template>

    </v-app-bar>
    <div style="background-color:#EBEFF3;height:88vh;overflow-y:auto;display: flex;justify-content: center;">
        <br>

        <v-card width="1000" class="ma-5" height="750" v-if="tab == 0">
            <v-toolbar dark color="green" flat>
                <v-toolbar-title>Stock Convertion List </v-toolbar-title>
                <v-spacer></v-spacer>
                <h3 class="mr-5">Total Raw Stock : {{parseFloat(rawStock).toFixed(2)}} Tonnes</h3>
                <v-btn color="blue" dark class="mr-5" @click="dialogNewProduct = true">
                    <v-icon left>
                        add
                    </v-icon>
                    Create Stock
                </v-btn>
                <download-csv :data="products">
                    <v-btn color="red" dark class="mr-5">
                        <v-icon left>
                            add
                        </v-icon>
                        Export
                    </v-btn>
                </download-csv>

            </v-toolbar>

            <v-data-table :headers="headersProducts" :items="products" :search="search" sort-by="stockCreationId" sort-desc>
                <template v-slot:top>
                    <v-toolbar flat color="white">

                        <v-spacer></v-spacer>
                        <v-btn color="blue" dark class="mr-5" @click="deliveryToday">
                            <v-icon left>
                                search
                            </v-icon>
                            Today
                        </v-btn>
                        <v-btn color="blue" dark class="mr-5" @click="dialogSpecific = true">
                            <v-icon left>
                                search
                            </v-icon>
                            Specfic Date
                        </v-btn>
                    </v-toolbar>
                </template>

                <template v-slot:item.createdBags="{ item }">

                    <v-chip color="green" dark>
                        {{ item.createdBags }} Bags
                    </v-chip>

                </template>
                <template v-slot:item.materialUsed="{ item }">

                    <v-chip color="orange" dark>
                        {{ parseFloat(item.materialUsed).toFixed(2) }} T
                    </v-chip>

                </template>

                <template v-slot:no-data>
                    <h3>No Records Found</h3>
                </template>

            </v-data-table>

        </v-card>

    </div>

    <v-dialog v-model="dialogNewProduct" max-width="750px">

        <v-card>
            <v-card-title class="headline blue lighten-2 white--text" primary-title>
                <span class="headline">Stock Creation Form </span>
                <v-spacer></v-spacer>

            </v-card-title>

            <v-card-text>
                <v-container>
                    <v-row>
                        <v-col cols="12" sm="12" md="6">
                            <div style="display: flex;justify-content: start;">
                                <span>Delivery Date</span>
                            </div>
                            <v-text-field v-model="stock.docDate" solo outlined type="date"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="12" md="6">
                            <div style="display: flex;justify-content: start;">
                                <span>Select Product</span>
                            </div>
                            <v-autocomplete item-text="productName" item-value="productId" v-model="stock.productId" :items="products2" solo outlined></v-autocomplete>

                        </v-col>
                        <v-col cols="12" sm="12" md="6">
                            <v-text-field type="number" v-model="stock.materialUsed" label="Raw Material Weight (Tonnes)" outlined></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="12" md="6">
                            <v-text-field type="number" v-model="stock.createdBags" label="Number of bags" outlined></v-text-field>
                        </v-col>

                    </v-row>
                </v-container>
            </v-card-text>

            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="red darken-1" dark @click="dialogNewProduct = false">Cancel</v-btn>
                <v-btn color="blue darken-1" dark @click="saveProduct">Save</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>

    <v-dialog v-model="dialogDelete" max-width="500px">
        <v-card>
            <v-card-title class="headline">Are you sure you want to delete this item?</v-card-title>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
            </v-card-actions>
        </v-card>
    </v-dialog>

    <v-dialog v-model="dialogSpecific" max-width="600px">

        <v-card>
            <v-card-title class="headline red lighten-2 white--text" primary-title>
                <span class="headline">Search Stock Creation by Date</span>
                <v-spacer></v-spacer>

            </v-card-title>

            <v-container>
                <v-row>
                    <v-col cols="12" sm="12" md="12">
                        <v-text-field type="date" v-model="startDate" solo outlined></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="12" md="12">
                        <v-text-field type="date" v-model="endDate" solo outlined></v-text-field>
                    </v-col>
                </v-row>
            </v-container>

            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="red darken-1" dark @click="dialogSpecific = false">Cancel</v-btn>
                <v-btn color="blue darken-1" dark @click="deliverySpecific">Save</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>

    <v-dialog v-model="dialogloader" hide-overlay persistent width="300">
        <v-card color="#003263" dark>
            <v-card-text>
                <br>
                <h3>Please stand by</h3>
                <br>
                <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
                <br>
            </v-card-text>
        </v-card>
    </v-dialog>

</div>
</template>

<script>
export default {
    data() {
        return {
            description: '',
            rawMaterial: 'Raw Material',
            log_cost: 0,
            log_totalCost: 0,
            log_weight: 0,
            deliveryNumner: '',
            deliveryDate: '',
            supplierName: '',
            suppilerAddress: '',
            supplierEmail: '',
            dialogPrint: false,

            startDate: '',
            endDate: '',
            dialogSpecific: false,
            dialogloader: false,
            dialogNewProduct: false,
            dialogNewProduct2: false,
            dialogDelete: false,
            currency: 'USD',
            nodeApi: '',
            selectedCustomer: '',
            tab: 0,
            items: ['Stock Creation'],
            search: '',
            pagination: {
                itemsPerPage: 15,

            },

            stock: {
                docDate: '',
                productId: '',
                materialUsed: 0,
                createdBags: 0,

            },

            customers: [],
            snack: false,
            snackColor: '',
            snackText: '',
            weight: 0,
            cost: 0,
            max25chars: v => v.length <= 25 || 'Input too long!',

            headersProducts: [{

                    text: 'Creation ID',
                    align: 'start',
                    sortable: false,
                    value: 'stockCreationId',
                },
                {
                    text: 'Creation Date',
                    align: 'start',
                    sortable: false,
                    value: 'docDate',
                },
                {
                    text: 'Product Name',
                    align: 'start',
                    sortable: false,
                    value: 'productName',
                },

                {
                    text: 'Material Used Size (Tonnes)',
                    align: 'start',
                    sortable: false,
                    value: 'materialUsed',
                },
                {
                    text: 'Stock Created',
                    align: 'start',
                    sortable: false,
                    value: 'createdBags',
                }

            ],

            lineItems: [],
            editedIndex: -1,
            products: [],
            products2: [],
            productName: '',
            productCurrency: '',
            productSize: '',
            productPrice: '',
            productId: '',
            itemProducts: [],
            docCurrency: '1',
            salesOrdersData: [],
            logisticsId: '',
            rawStock: 0

        }
    },
    mounted() {

        this.getProducts()
        this.getProducts2()
        this.getRawStock()
        const today = new Date();
        const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
        this.stock.docDate = date
    },
    watch: {

        dialogNewProduct(val) {
            if (val) {

                this.stock = {
                    docDate: '',
                    productId: '',
                    materialUsed: 0,
                    createdBags: 0,

                }
            }
        },

    },
    methods: {

        saveProduct() {

            if (!this.stock.docDate) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Stock Failed',
                    text: 'Please enter a valid document Date.'
                })
            } else if (!this.stock.productId) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Stock Failed',
                    text: 'Please enter a valid product.'
                })
            } else if (!this.stock.materialUsed) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Stock Failed',
                    text: 'Please enter a valid material amount'
                })
            } else if (!this.stock.createdBags) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Stock Failed',
                    text: 'Please enter a valid stock converted amount'
                })
            } else if (isNaN(this.stock.materialUsed)) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Stock Failed',
                    text: 'Please enter a valid Raw Material size (it has to be a number)'
                })
            } else if (isNaN(this.stock.createdBags)) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Stock Failed',
                    text: 'Please enter a valid Product size (it has to be a number)'
                })
            } else if (this.rawStock < this.stock.materialUsed) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Stock Failed',
                    text: 'Sorry you are trying to convert more material than you have in stock.'
                })
            } else {
                //console.log(this.productDetails)
                this.dialogloader = true
                this.stock.createdBy = this.$session.get('user').userId
                // console.log(this.editedUser)
                this.axios.post(this.nodeApi + '/addNewStocks', this.stock).then((response) => {
                    // console.log(response.data)
                    if (response.data.length > 0) {
                        this.getProducts()
                        this.getProducts2()
                        this.getRawStock()
                        this.dialogNewProduct = false
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'success',
                            title: 'Stock Added',
                            text: 'You have successfully added a new Delivery note'
                        })

                        this.dialogloader = false

                    } else {
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'error',
                            title: 'Adding Stock Failed',
                            text: response.data.originalError.info.message
                        })
                    }

                }).catch(err => {
                    this.dialogloader = false
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding Delivery Failed',
                        text: err
                    })
                })

            }
        },
        getProducts2() {
            this.axios.get(this.nodeApi + '/getAllProducts').then((response) => {
                this.products2 = response.data
                //   console.log(this.products2)

            }).catch(err => {
                console.log(err)
            })

        },

        closeDelete() {
            this.dialogDelete = false
            this.$nextTick(() => {

                this.editedIndex = -1
            })
        },
        deleteItem(item) {
            this.editedIndex = this.products.indexOf(item)
            this.logisticsId = item.logisticsId
            this.dialogDelete = true
        },

        deleteItemConfirm() {
            this.products.splice(this.editedIndex, 1)
            this.axios.post(this.nodeApi + '/removeDeliveryIn', {
                logisticsId: this.logisticsId
            }).then((response) => {
                // console.log(response.data)
                if (response.data.affectedRows > 0) {
                    this.$swal.fire({
                        type: 'success',
                        title: 'Item Deleted successfully',
                        text: 'You have successfully added a new sales order'
                    })
                    this.closeDelete()

                } else {

                    this.$swal.fire({
                        type: 'error',
                        title: 'Item Deleting Failed',
                        text: response.data.originalError.info.message
                    })
                }

            }).catch(err => {

                this.$swal.fire({
                    type: 'error',
                    title: 'Item Deleting Failed',
                    text: err
                })
            })

        },

        getProducts() {
            this.axios.get(this.nodeApi + '/getAllCreatedProducts').then((response) => {
                this.products = response.data

            }).catch(err => {
                console.log(err)
            })

        },
        getRawStock() {
            this.axios.get(this.nodeApi + '/getRawStock').then((response) => {
                this.rawStock = response.data[0].tonnes

            }).catch(err => {
                console.log(err)
            })

        },
        deliveryToday() {
            this.axios.get(this.nodeApi + '/getAllCreatedProductsToday').then((response) => {

                this.products = response.data
            }).catch(err => {
                console.log(err)
            })

        },
        deliverySpecific() {
            this.axios.post(this.nodeApi + '/getAllCreatedProductsSpecific', {
                startDate: this.startDate,
                endDate: this.endDate
            }).then((response) => {

                this.products = response.data
                this.dialogSpecific = false

            }).catch(err => {
                console.log(err)
            })

        },

    },
}
</script>

