                            </td>

                            <td style="text-align: right;">
                                Total General Expenses: <b>{{parseFloat(statement.GtotalExpenses).toFixed(2)}}</b>
                            </td>
                        </tr>
                        <br>
                        <br>
                        <br>
                        <tr class="total">
                            <td>

                            </td>
                            <td>

                            </td>
                            <td>

                            </td>
                            <td>

                            </td>

                            <td style="text-align: right;">
                                Net Profit: <b>{{parseFloat(statement.totalProfit).toFixed(2)}}</b>
                            </td>

                        </tr>
                    </table>
                </div>

            </v-card-text>

        </v-card>

    </v-dialog>
    <img :src="output">

</div>
</template>

<script>
import jsPDF from 'jspdf'
import 'jspdf-autotable'

const doc = new jsPDF('p', 'mm', 'a4')
var width = doc.internal.pageSize.getWidth();
var height = doc.internal.pageSize.getHeight();

export default {
    data() {
        return {
            output: null,
            dialogSpecific2: false,
            dialogPrint: false,
            dialogPrintFull: false,
            statement: {
                fromDate: '',
                toDate: '',
                totalIncome: 0,
                totalExpenses: 0,
                GtotalExpenses: 0,
                totalProfit: 0,
                docCurrency: '1',
                ledgerIncome: [],
                ledgerExpenses: [],
                ledgerGExpenses: [],
            },
            statementDocCurrency: '1',
            startDate: '',
            endDate: '',
            dialogSpecific: false,
            expenseName: '',
            dialogloader: false,
            dialogNewProduct: false,
            dialogNewProduct2: false,
            dialogDelete: false,
            dialogDelete2: false,
            currency: 'USD',
            nodeApi: '',
            selectedCustomer: '',
            tab: 0,
            items: ['Expenses', 'Ledger Report'],
            search: '',
            pagination: {
                itemsPerPage: 15,

            },
            productDetails: {
                productCurrency: '1',
                productName: '',
                productSize: '',
                productPrice: ''
            },
            deliverNote: {
                docDate: '',
                expenseName: '',
                cost: '',
                docCurrency: '1',

            },
            salesOrder: {

                docDate: '',
                docCurrency: '',
                description: '',
                itemData: [],
                docTotal: 0
            },
            customers: [],
            snack: false,
            snackColor: '',
            snackText: '',
            max25chars: v => v.length <= 25 || 'Input too long!',

            headersProducts: [{

                    text: 'Expense ID',
                    align: 'start',
                    sortable: false,
                    value: 'id',
                },
                {
                    text: 'Journal Date',
                    align: 'start',
                    sortable: false,
                    value: 'docDate',
                },
                {
                    text: 'Expense Name',
                    align: 'start',
                    sortable: false,
                    value: 'description',
                },

                {
                    text: 'Amount',
                    align: 'start',
                    sortable: false,
                    value: 'credit',
                },
                {
                    text: 'Actions',
                    value: 'actions'
                },

            ],
            headersAccount: [{

                    text: 'Ledger ID',
                    align: 'start',
                    sortable: false,
                    value: 'id',
                },
                {
                    text: 'Ledger Date',
                    align: 'start',
                    sortable: false,
                    value: 'docDate',
                },
                {
                    text: 'Description',
                    align: 'start',
                    sortable: false,
                    value: 'description',
                },

                {
                    text: 'Debit (Income)',
                    align: 'start',
                    sortable: false,
                    value: 'debit',
                },
                {
                    text: 'Credit (Expense)',
                    align: 'start',
                    sortable: false,
                    value: 'credit',
                },
                {
                    text: 'Actions',
                    value: 'actions'
                },

            ],

            lineItems: [],
            editedIndex: -1,
            products: [],
            productName: '',
            productCurrency: '',
            productSize: '',
            productPrice: '',
            productId: '',
            itemProducts: [],
            docCurrency: '1',
            salesOrdersData: [],
            expenseId: '',
            ledgerDara: []

        }
    },
    mounted() {
        this.initialize()
        this.getProducts()
        this.getLedgerData()
        this.getAllOrders()
        const today = new Date();
        const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
        this.salesOrder.docDate = date
    },
    watch: {
        statementDocCurrency(val) {
            console.log(val)
        },
        docCurrency(val) {
            let data = val
            this.salesOrder.docCurrency = data
            if (val == '1') {
                this.currency = 'USD'
                this.deliverNote.docCurrency = data
            }
            if (val == '2') {
                this.currency = 'ZWL'
                this.deliverNote.docCurrency = data
            }
            this.clearForm()
            const today = new Date();
            const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
            this.deliverNote.docDate = date
            // console.log(this.salesOrder.docCurrency)
            this.getProducts()
            this.getLedgerData()
        },
        dialogNewProduct(val) {
            if (val) {
                this.deliverNote = {
                    docDate: '',
                    expenseName: '',
                    cost: '',
                    docCurrency: '1',
                }
                const today = new Date();
                const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
                this.deliverNote.docDate = date
            }
        },
        dialogNewProduct2(val) {
            if (val) {
                this.productDetails = {
                    productCurrency: '1',
                    productName: '',
                    productSize: '',
                    productPrice: ''
                }
            }
        },

    },
    methods: {
        async printPDF() {
            // doc.save('IcomeStatement.pdf')
            const el = this.$refs.printMe;
            // add option type to get the image version
            // if not provided the promise will return 
            // the canvas.
            const options = {
                type: 'dataURL'
            }
            this.output = await this.$html2canvas(el, options);

            await doc.addImage(this.output, 'png', 0, 0, width, height)
            await doc.save('IcomeStatement.pdf')
        },
        printStatement() {
            if (this.statement.fromDate == '' && this.statement.toDate == '') {
                const today = new Date();
                const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();

                this.statement.fromDate = date
                this.statement.toDate = date
                this.dialogPrint = true

                console.log(this.ledgerDara)
                this.statement.totalIncome = 0
                this.statement.totalExpenses = 0
                this.statement.GtotalExpenses = 0
                this.statement.totalProfit = 0
                this.statement.docCurrency = this.statementDocCurrency
                this.ledgerDara.forEach(element => {
                    if (element.debit > 0 && element.currency == this.statement.docCurrency) {
                        this.statement.totalIncome += element.debit
                    }
                    if (element.credit > 0 && element.currency == this.statement.docCurrency && element.docId == 0) {
                        this.statement.GtotalExpenses += element.credit
                    }
                    if (element.credit > 0 && element.currency == this.statement.docCurrency && element.docId != 0) {
                        this.statement.totalExpenses += element.credit
                    }
                })
                this.statement.totalProfit = this.statement.totalIncome - (this.statement.totalExpenses + this.statement.GtotalExpenses)
            } else {
                this.dialogPrint = true
                //   console.log(this.ledgerDara)
                this.statement.totalIncome = 0
                this.statement.totalExpenses = 0
                this.statement.GtotalExpenses = 0
                this.statement.totalProfit = 0
                this.statement.docCurrency = this.statementDocCurrency
                this.ledgerDara.forEach(element => {
                    if (element.debit > 0 && element.currency == this.statement.docCurrency) {
                        this.statement.totalIncome += element.debit
                    }
                    if (element.credit > 0 && element.currency == this.statement.docCurrency && element.docId == 0) {
                        this.statement.GtotalExpenses += element.credit
                    }
                    if (element.credit > 0 && element.currency == this.statement.docCurrency && element.docId != 0) {
                        this.statement.totalExpenses += element.credit
                    }
                })
                this.statement.totalProfit = this.statement.totalIncome - (this.statement.totalExpenses + this.statement.GtotalExpenses)
            }

        },
        printStatementFull() {
            if (this.statement.fromDate == '' && this.statement.toDate == '') {
                const today = new Date();
                const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();

                this.statement.fromDate = date
                this.statement.toDate = date
                this.dialogPrintFull = true

                //console.log(this.ledgerDara)
                this.statement.totalIncome = 0
                this.statement.totalExpenses = 0
                this.statement.GtotalExpenses = 0
                this.statement.totalProfit = 0
                this.statement.ledgerIncome = []
                this.statement.ledgerGExpenses = []
                this.statement.ledgerExpenses = []
                this.statement.docCurrency = this.statementDocCurrency

                this.ledgerDara.forEach(element => {
                    if (element.debit > 0 && element.currency == this.statement.docCurrency) {
                        this.statement.totalIncome += element.debit
                        this.statement.ledgerIncome.push(element)
                    }
                    if (element.credit > 0 && element.currency == this.statement.docCurrency && element.docId == 0) {
                        this.statement.GtotalExpenses += element.credit
                        this.statement.ledgerGExpenses.push(element)
                    }
                    if (element.credit > 0 && element.currency == this.statement.docCurrency && element.docId != 0) {
                        this.statement.totalExpenses += element.credit
                        this.statement.ledgerExpenses.push(element)
                    }
                })
                this.statement.totalProfit = this.statement.totalIncome - (this.statement.totalExpenses + this.statement.GtotalExpenses)
            } else {
                this.dialogPrintFull = true
                // console.log(this.ledgerDara)
                this.statement.totalIncome = 0
                this.statement.totalExpenses = 0
                this.statement.GtotalExpenses = 0
                this.statement.totalProfit = 0
                this.statement.ledgerIncome = []
                this.statement.ledgerGExpenses = []
                this.statement.ledgerExpenses = []
                this.statement.docCurrency = this.statementDocCurrency
                this.ledgerDara.forEach(element => {
                    if (element.debit > 0 && element.currency == this.statement.docCurrency) {
                        this.statement.totalIncome += element.debit
                        this.statement.ledgerIncome.push(element)
                    }
                    if (element.credit > 0 && element.currency == this.statement.docCurrency && element.docId == 0) {
                        this.statement.GtotalExpenses += element.credit
                        this.statement.ledgerGExpenses.push(element)
                    }
                    if (element.credit > 0 && element.currency == this.statement.docCurrency && element.docId != 0) {
                        this.statement.totalExpenses += element.credit
                        this.statement.ledgerExpenses.push(element)
                    }
                })
                this.statement.totalProfit = this.statement.totalIncome - (this.statement.totalExpenses + this.statement.GtotalExpenses)
            }

        },
        editItem(data) {

            this.productDetails = Object.assign({}, data)
            this.productName = data.productName
            this.productCurrency = data.productCurrency.toString()
            this.productSize = data.productSize
            this.productPrice = data.productPrice
            this.productId = data.productId
            //console.log(this.productCurrency)
            this.dialogNewProduct2 = true
        },
        saveProductUpdate() {
            if (!this.expenseName) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Expense Item Failed',
                    text: 'Please enter a valid Expense Item.'
                })
            } else {

                // console.log(this.editedUser)
                this.axios.post(this.nodeApi + '/addExpense', {
                    expenseName: this.expenseName
                }).then((response) => {
                    // console.log(response.data)
                    if (response.data.affectedRows > 0) {
                        this.initialize()
                        this.dialogNewProduct2 = false
                        this.dialogloader = false
                        this.expenseName = ''
                        this.$swal.fire({
                            type: 'success',
                            title: 'Expense Item Added',
                            text: 'You have successfully added a Expense Item'
                        })
                    } else {
                        this.$swal.fire({
                            type: 'error',
                            title: 'Adding Expense Item Failed',
                            text: response.data.originalError.info.message
                        })
                    }

                }).catch(err => {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding Expense Item Failed Failed',
                        text: err
                    })
                })

            }
        },
        saveProduct() {

            if (!this.deliverNote.docDate) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Journal Failed',
                    text: 'Please enter a valid document Date.'
                })
            } else if (!this.deliverNote.expenseName) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Journal Failed',
                    text: 'Please enter a valid supplier.'
                })
            } else if (!this.deliverNote.cost) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Journal Failed',
                    text: 'Please enter a valid cost amount'
                })
            } else if (isNaN(this.deliverNote.cost)) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Journal Failed',
                    text: 'Please enter a valid Product price (it has to be a number)'
                })
            } else {
                //console.log(this.productDetails)
                this.dialogloader = true
                this.deliverNote.createdBy = this.$session.get('user').userId
                // console.log(this.editedUser)
                this.axios.post(this.nodeApi + '/addJournalExpense', this.deliverNote).then((response) => {
                    // console.log(response.data)
                    if (response.data.affectedRows > 0) {
                        this.getProducts()
                        this.getLedgerData()
                        this.dialogNewProduct = false
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'success',
                            title: 'Journal Added',
                            text: 'You have successfully added a new Journal note'
                        })
                    } else {
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'error',
                            title: 'Adding Journal Failed',
                            text: response.data.originalError.info.message
                        })
                    }

                }).catch(err => {
                    this.dialogloader = false
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding Delivery Failed',
                        text: err
                    })
                })

            }
        },
        addItem() {
            this.lineItems.push({
                productId: '1',
                productName: 'Click to Edit',
                quantity: 1,
                price: 0,
                tax: 14.5,
                linetotal: 0,
            })
        },
        clearForm() {
            this.salesOrder = {
                customerId: '',
                docDate: '',
                docCurrency: '1',
                description: '',
                itemData: [],
                docTotal: 0
            }
            this.lineItems = []

            const today = new Date();
            const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
            this.salesOrder.docDate = date
        },
        closeDelete() {
            this.dialogDelete = false
            this.dialogDelete2 = false
            this.$nextTick(() => {

                this.editedIndex = -1
            })
        },
        deleteItem(item) {
            this.editedIndex = this.products.indexOf(item)
            this.expenseId = item.id
            this.dialogDelete = true
        },

        deleteItemConfirm() {
            this.products.splice(this.editedIndex, 1)
            this.axios.post(this.nodeApi + '/removeExpenseIdList', {
                expenseId: this.expenseId
            }).then((response) => {
                // console.log(response.data)
                if (response.data.affectedRows > 0) {
                    this.$swal.fire({
                        type: 'success',
                        title: 'Journal Deleted successfully',
                        text: 'You have successfully added a new sales order'
                    })
                    this.closeDelete()

                } else {

                    this.$swal.fire({
                        type: 'error',
                        title: 'Journal Deleting Failed',
                        text: response.data.originalError.info.message
                    })
                }

            }).catch(err => {

                this.$swal.fire({
                    type: 'error',
                    title: 'Item Deleting Failed',
                    text: err
                })
            })
        },
        deleteItem2(item) {
            this.editedIndex = this.ledgerDara.indexOf(item)
            this.expenseId = item.id
            this.dialogDelete2 = true
        },

        deleteItemConfirm2() {
            this.ledgerDara.splice(this.editedIndex, 1)
            this.axios.post(this.nodeApi + '/removeExpenseIdList', {
                expenseId: this.expenseId
            }).then((response) => {
                // console.log(response.data)
                if (response.data.affectedRows > 0) {
                    this.$swal.fire({
                        type: 'success',
                        title: 'Ledger Deleted successfully',
                        text: 'You have successfully added a new sales order'
                    })
                    this.closeDelete()

                } else {

                    this.$swal.fire({
                        type: 'error',
                        title: 'Ledger Deleting Failed',
                        text: response.data.originalError.info.message
                    })
                }

            }).catch(err => {

                this.$swal.fire({
                    type: 'error',
                    title: 'Item Deleting Failed',
                    text: err
                })
            })
        },
        getAllOrders() {
            this.axios.get(this.nodeApi + '/getAllSalesOrders').then((response) => {
                this.salesOrdersData = response.data

            }).catch(err => {
                console.log(err)
            })
        },
        getLedgerData() {
            this.axios.get(this.nodeApi + '/getLedgerReport').then((response) => {
                this.ledgerDara = response.data

            }).catch(err => {
                console.log(err)
            })
        },
        getProducts() {
            this.axios.get(this.nodeApi + '/getJournalExpenses').then((response) => {
                this.products = response.data

            }).catch(err => {
                console.log(err)
            })

        },
        initialize() {
            this.axios.get(this.nodeApi + '/getAllExpenses').then((response) => {

                this.customers = response.data
            }).catch(err => {
                console.log(err)
            })

        },
        journalsToday() {
            this.axios.get(this.nodeApi + '/getJournalExpensesToday').then((response) => {

                this.products = response.data
            }).catch(err => {
                console.log(err)
            })

        },
        journalsSpecific() {
            this.axios.post(this.nodeApi + '/getJournalExpensesSpecific', {
                startDate: this.startDate,
                endDate: this.endDate
            }).then((response) => {

                this.products = response.data
                this.statement.fromDate = this.startDate
                this.statement.toDate = this.endDate
                this.dialogSpecific = false

            }).catch(err => {
                console.log(err)
            })

        },

        getLedgerDataToday() {
            this.axios.get(this.nodeApi + '/getLedgerReportToday').then((response) => {

                this.ledgerDara = response.data
            }).catch(err => {
                console.log(err)
            })

        },
        getLedgerDataSpecific() {
            this.axios.post(this.nodeApi + '/getLedgerReportSpecific', {
                startDate: this.startDate,
                endDate: this.endDate
            }).then((response) => {

                this.ledgerDara = response.data
                this.dialogSpecific2 = false

            }).catch(err => {
                console.log(err)
            })

        },
        save(data) {
            console.log(data)

            const result = this.products.find(({
                productName
            }) => productName === data.productName);
            let index = this.lineItems.indexOf(data)
            if (data.productName == result.productName) {
                this.lineItems[index].productId = result.productId
                this.lineItems[index].price = result.productPrice
                this.lineItems[index].productSize = result.productSize

                this.lineItems[index].linetotal = (result.productPrice * data.quantity) + (((result.productPrice * data.quantity) * data.tax) / 100)
                this.lineItems.forEach(element => {
                    this.salesOrder.docTotal += element.linetotal
                })

            } else {
                console.log('Data Mismatch')
            }

            this.snack = true
            this.snackColor = 'success'
            this.snackText = 'Data saved'
        },
        cancel() {
            this.snack = true
            this.snackColor = 'error'
            this.snackText = 'Canceled'
        },
        open() {
            this.snack = true
            this.snackColor = 'info'
            this.snackText = 'Dialog opened'
        },
        close() {
            console.log('Dialog closed')
        },

        saveSalesOrder() {
            this.dialogloader = true
            this.salesOrder.itemData = JSON.stringify(this.lineItems)
            this.salesOrder.docCurrency = this.docCurrency
            this.salesOrder.createdBy = this.$session.get('user').userId
            // console.log(this.salesOrder)

            this.axios.post(this.nodeApi + '/addSalesOrder', this.salesOrder).then((response) => {
                // console.log(response.data)
                if (response.data.length > 0) {
                    this.$swal.fire({
                        type: 'success',
                        title: 'Sales Order Added',
                        text: 'You have successfully added a new sales order'
                    })
                    this.clearForm()
                    this.getAllOrders()
                    this.dialogloader = false
                } else {
                    this.dialogloader = false
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding Sales Order Failed',
                        text: response.data.originalError.info.message
                    })
                }

            }).catch(err => {
                this.dialogloader = false
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Sales Order Failed',
                    text: err
                })
            })

        }
    },
}
</script>

