<script>
export default {
    data() {
        return {
            description: '',
            rawMaterial: 'Raw Material',
            log_cost: 0,
            log_totalCost: 0,
            log_weight: 0,
            deliveryNumner: '',
            deliveryDate: '',
            supplierName: '',
            suppilerAddress: '',
            supplierEmail: '',
            dialogPrint: false,

            startDate: '',
            endDate: '',
            dialogSpecific: false,
            dialogloader: false,
            dialogNewProduct: false,
            dialogNewProduct2: false,
            dialogDelete: false,
            currency: 'USD',
            nodeApi: '',
            selectedCustomer: '',
            tab: 0,
            items: ['Delivery In'],
            search: '',
            pagination: {
                itemsPerPage: 15,

            },
            productDetails: {
                productCurrency: '1',
                productName: '',
                productSize: '',
                productPrice: ''
            },
            deliverNote: {
                docDate: '',
                supplierId: '',
                weight: 0,
                cost: 0,
                totalCost: 0,
                docCurrency: '1',
                description: ''

            },
            salesOrder: {

                docDate: '',
                docCurrency: '',
                description: '',
                itemData: [],
                docTotal: 0
            },
            customers: [],
            snack: false,
            snackColor: '',
            snackText: '',
            weight: 0,
            cost: 0,
            max25chars: v => v.length <= 25 || 'Input too long!',

            headersProducts: [{

                    text: 'Delivery ID',
                    align: 'start',
                    sortable: false,
                    value: 'logisticsId',
                },
                {
                    text: 'Delivery Date',
                    align: 'start',
                    sortable: false,
                    value: 'docDate',
                },
                {
                    text: 'Supplier Name',
                    align: 'start',
                    sortable: false,
                    value: 'supplierName',
                },

                {
                    text: 'Material Szie (Tonnes)',
                    align: 'start',
                    sortable: false,
                    value: 'weight',
                },
                {
                    text: 'Cost Per Tonne',
                    align: 'start',
                    sortable: false,
                    value: 'cost',
                },
                {
                    text: 'Total Cost',
                    align: 'start',
                    sortable: false,
                    value: 'totalCost',
                },
                {
                    text: 'Actions',
                    value: 'actions'
                },

            ],
            headers: [{
                    text: 'Product ID',
                    align: 'start',
                    sortable: false,
                    value: 'productId',
                },
                {
                    text: 'Product Name',
                    align: 'start',
                    sortable: false,
                    value: 'productName',
                },
                {
                    text: 'Quantity',
                    value: 'quantity'
                },
                {
                    text: 'Size',
                    value: 'productSize'
                },
                {
                    text: 'Unit Price',
                    value: 'price'
                },
                {
                    text: 'TAX',
                    value: 'tax'
                },
                {
                    text: 'Line Total (Inc Tax)',
                    value: 'linetotal'
                },
                {
                    text: 'Actions',
                    value: 'actions'
                },
            ],
            headersSales: [{

                    text: 'SalesOrder ID',
                    align: 'start',
                    sortable: false,
                    value: 'salesOrderId',
                },
                {
                    text: 'Sales Date',
                    align: 'start',
                    sortable: false,
                    value: 'docDate',
                },
                {
                    text: 'Customer Name',
                    align: 'start',
                    sortable: false,
                    value: 'customerName',
                },
                {
                    text: 'Sales Total',
                    align: 'start',
                    sortable: false,
                    value: 'docTotal',
                },

            ],
            lineItems: [],
            editedIndex: -1,
            products: [],
            productName: '',
            productCurrency: '',
            productSize: '',
            productPrice: '',
            productId: '',
            itemProducts: [],
            docCurrency: '1',
            salesOrdersData: [],
            logisticsId: ''

        }
    },
    mounted() {
        this.initialize()
        this.getProducts()
        this.getAllOrders()
        const today = new Date();
        const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
        this.deliverNote.docDate = date
    },
    watch: {
        weight(val) {
            this.deliverNote.weight = val
            this.deliverNote.totalCost = val * this.cost
        },
        cost(val) {
            this.deliverNote.cost = val
            this.deliverNote.totalCost = val * this.weight
        },
        docCurrency(val) {
            let data = val
            this.salesOrder.docCurrency = data
            if (val == '1') {
                this.currency = 'USD'
                this.salesOrder.docCurrency = data
            }
            if (val == '2') {
                this.currency = 'ZWL'
                this.salesOrder.docCurrency = data
            }
            this.clearForm()
            const today = new Date();
            const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
            this.deliverNote.docDate = date
            // console.log(this.salesOrder.docCurrency)
            this.getProducts()
        },
        dialogNewProduct(val) {
            if (val) {
                this.weight = 0
                this.cost = 0
                this.deliverNote = {
                    docDate: '',
                    supplierId: '',
                    weight: 0,
                    cost: 0,
                    totalCost: 0,
                    docCurrency: '1',
                    description: ''

                }
            }
        },
        dialogNewProduct2(val) {
            if (val) {
                this.productDetails = {
                    productCurrency: '1',
                    productName: '',
                    productSize: '',
                    productPrice: ''
                }
            }
        },

    },
    methods: {

        editItem(data) {

            this.productDetails = Object.assign({}, data)
            this.productName = data.productName
            this.productCurrency = data.productCurrency.toString()
            this.productSize = data.productSize
            this.productPrice = data.productPrice
            this.productId = data.productId
            console.log(this.productCurrency)
            this.dialogNewProduct2 = true
        },
        saveProductUpdate() {
            if (!this.productName) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Updating Product Failed',
                    text: 'Please enter a valid Product Name.'
                })
            } else if (!this.productSize) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Updating Product Failed',
                    text: 'Please enter a valid Product package size.'
                })
            } else if (!this.productPrice) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Updating Product Failed',
                    text: 'Please enter a valid Product price'
                })
            } else if (isNaN(this.productSize)) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Updating Product Failed',
                    text: 'Please enter a valid Product size (it has to be a number)'
                })
            } else if (isNaN(this.productPrice)) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Updating Product Failed',
                    text: 'Please enter a valid Product price (it has to be a number)'
                })
            } else {
                //console.log(this.productDetails)
                this.dialogloader = true
                this.productDetails.productCurrency = this.productCurrency
                this.productDetails.productName = this.productName
                this.productDetails.productSize = this.productSize
                this.productDetails.productPrice = this.productPrice
                this.productDetails.productId = this.productId
                this.productDetails.createdBy = this.$session.get('user').userId
                // console.log(this.editedUser)
                this.axios.post(this.nodeApi + '/updateProduct', this.productDetails).then((response) => {
                    // console.log(response.data)
                    if (response.data.affectedRows > 0) {
                        this.getProducts()
                        this.dialogNewProduct2 = false
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'success',
                            title: 'Product Updated',
                            text: 'You have successfully updated a product'
                        })
                    } else {
                        this.$swal.fire({
                            type: 'error',
                            title: 'Updating Product Failed',
                            text: response.data.originalError.info.message
                        })
                    }

                }).catch(err => {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Updating Product Failed',
                        text: err
                    })
                })

            }
        },
        saveProduct() {

            if (!this.deliverNote.docDate) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Delivery Failed',
                    text: 'Please enter a valid document Date.'
                })
            } else if (!this.deliverNote.supplierId) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Delivery Failed',
                    text: 'Please enter a valid supplier.'
                })
            } else if (!this.deliverNote.cost) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Delivery Failed',
                    text: 'Please enter a valid cost amount'
                })
            } else if (!this.deliverNote.description) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Delivery Failed',
                    text: 'Please enter a valid description'
                })
            } else if (!this.deliverNote.weight) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Delivery Failed',
                    text: 'Please enter a valid weight amount'
                })
            } else if (isNaN(this.deliverNote.weight)) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Delivery Failed',
                    text: 'Please enter a valid Raw Material size (it has to be a number)'
                })
            } else if (isNaN(this.deliverNote.cost)) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Delivery Failed',
                    text: 'Please enter a valid Product price (it has to be a number)'
                })
            } else {
                //console.log(this.productDetails)
                this.dialogloader = true
                this.deliverNote.createdBy = this.$session.get('user').userId
                // console.log(this.editedUser)
                this.axios.post(this.nodeApi + '/addDeliveryIn', this.deliverNote).then((response) => {
                    // console.log(response.data)
                    if (response.data.length > 0) {
                        this.getProducts()
                        this.dialogNewProduct = false
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'success',
                            title: 'Delivery Added',
                            text: 'You have successfully added a new Delivery note'
                        })

                        this.dialogloader = false

                        this.deliveryNumner = response.data[1].insertId

                        let supplierId = this.deliverNote.supplierId
                        this.description = this.deliverNote.description

                        var result = this.customers.filter(item => {
                            if (item.supplierId == supplierId) {
                                return true
                            }
                        })

                        this.suppilerAddress = result[0].supplierAddress
                        this.supplierName = result[0].supplierName
                        this.supplierEmail = result[0].supplierEmail
                        //this.log_totalCost = this.deliverNote.totalCost

                        console.log(result)

                        var d = new Date(Date.now()),
                            month = '' + (d.getMonth() + 1),
                            day = '' + d.getDate(),
                            year = d.getFullYear();

                        this.deliveryDate = `${day}-${month}-${year}`
                        this.dialogPrint = true
                    } else {
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'error',
                            title: 'Adding Delivery Failed',
                            text: response.data.originalError.info.message
                        })
                    }

                }).catch(err => {
                    this.dialogloader = false
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding Delivery Failed',
                        text: err
                    })
                })

            }
        },
        addItem() {
            this.lineItems.push({
                productId: '1',
                productName: 'Click to Edit',
                quantity: 1,
                price: 0,
                tax: 14.5,
                linetotal: 0,
            })
        },
        clearForm() {
            this.salesOrder = {
                customerId: '',
                docDate: '',
                docCurrency: '1',
                description: '',
                itemData: [],
                docTotal: 0
            }
            this.lineItems = []

            const today = new Date();
            const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
            this.salesOrder.docDate = date
        },
        closeDelete() {
            this.dialogDelete = false
            this.$nextTick(() => {

                this.editedIndex = -1
            })
        },
        deleteItem(item) {
            this.editedIndex = this.products.indexOf(item)
            this.logisticsId = item.logisticsId
            this.dialogDelete = true
        },

        deleteItemConfirm() {
            this.products.splice(this.editedIndex, 1)
            this.axios.post(this.nodeApi + '/removeDeliveryIn', {
                logisticsId: this.logisticsId
            }).then((response) => {
                // console.log(response.data)
                if (response.data.affectedRows > 0) {
                    this.$swal.fire({
                        type: 'success',
                        title: 'Item Deleted successfully',
                        text: 'You have successfully added a new sales order'
                    })
                    this.closeDelete()

                } else {

                    this.$swal.fire({
                        type: 'error',
                        title: 'Item Deleting Failed',
                        text: response.data.originalError.info.message
                    })
                }

            }).catch(err => {

                this.$swal.fire({
                    type: 'error',
                    title: 'Item Deleting Failed',
                    text: err
                })
            })

        },
        getAllOrders() {
            this.axios.get(this.nodeApi + '/getAllSalesOrders').then((response) => {
                this.salesOrdersData = response.data

            }).catch(err => {
                console.log(err)
            })
        },
        getProducts() {
            this.axios.get(this.nodeApi + '/getAllDelieries').then((response) => {
                this.products = response.data

            }).catch(err => {
                console.log(err)
            })

        },
        deliveryToday() {
            this.axios.get(this.nodeApi + '/getAllDelieriesToday').then((response) => {

                this.products = response.data
            }).catch(err => {
                console.log(err)
            })

        },
        deliverySpecific() {
            this.axios.post(this.nodeApi + '/getAllDelieriesSpecific', {
                startDate: this.startDate,
                endDate: this.endDate
            }).then((response) => {

                this.products = response.data
                this.dialogSpecific = false

            }).catch(err => {
                console.log(err)
            })

        },

        initialize() {
            this.axios.get(this.nodeApi + '/getAllSuppliers').then((response) => {
                this.customers = response.data
            }).catch(err => {
                console.log(err)
            })

        },
        save(data) {
            console.log(data)

            const result = this.products.find(({
                productName
            }) => productName === data.productName);
            let index = this.lineItems.indexOf(data)
            if (data.productName == result.productName) {
                this.lineItems[index].productId = result.productId
                this.lineItems[index].price = result.productPrice
                this.lineItems[index].productSize = result.productSize

                this.lineItems[index].linetotal = (result.productPrice * data.quantity) + (((result.productPrice * data.quantity) * data.tax) / 100)
                this.lineItems.forEach(element => {
                    this.salesOrder.docTotal += element.linetotal
                })

            } else {
                console.log('Data Mismatch')
            }

            this.snack = true
            this.snackColor = 'success'
            this.snackText = 'Data saved'
        },
        cancel() {
            this.snack = true
            this.snackColor = 'error'
            this.snackText = 'Canceled'
        },
        open() {
            this.snack = true
            this.snackColor = 'info'
            this.snackText = 'Dialog opened'
        },
        close() {
            console.log('Dialog closed')
        },

        saveSalesOrder() {
            this.dialogloader = true
            this.salesOrder.itemData = JSON.stringify(this.lineItems)
            this.salesOrder.docCurrency = this.docCurrency
            this.salesOrder.createdBy = this.$session.get('user').userId
            // console.log(this.salesOrder)

            this.axios.post(this.nodeApi + '/addSalesOrder', this.salesOrder).then((response) => {
                // console.log(response.data)
                if (response.data.length > 0) {
                    this.$swal.fire({
                        type: 'success',
                        title: 'Sales Order Added',
                        text: 'You have successfully added a new sales order'
                    })
                    this.clearForm()
                    this.getAllOrders()
                    this.dialogloader = false
                } else {
                    this.dialogloader = false
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding Sales Order Failed',
                        text: response.data.originalError.info.message
                    })
                }

            }).catch(err => {
                this.dialogloader = false
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Sales Order Failed',
                    text: err
                })
            })

        }
    },
}
</script>

