                    productSize: '',
                    stock: ''
                }
            }
        },
        dialogNewProduct2(val) {
            if (val) {
                this.productDetails = {
                    productCurrency: '1',
                    productName: '',
                    stock: '',
                    productPrice: ''
                }
            }
        },

    },
    methods: {
        printPage() {
            // var mywindow = window.open('', 'PRINT', 'height=400,width=600');

            // mywindow.document.write('<html><head><title>' + document.title + '</title>');
            // mywindow.document.write('</head><body >');
            // mywindow.document.write('<h1>' + document.title + '</h1>');
            // mywindow.document.write(document.getElementById('invoiceR').innerHTML);
            // mywindow.document.write('</body></html>');

            // mywindow.document.close(); // necessary for IE >= 10
            // mywindow.focus(); // necessary for IE >= 10*/

            // mywindow.print();
            // mywindow.close();

            this.dialogPrint = false
            //return true;

        },
        editItem(data) {

            this.productDetails = Object.assign({}, data)
            this.productName = data.productName
            this.productCurrency = data.productCurrency.toString()
            this.productSize = data.productSize
            this.productPrice = data.productPrice
            this.productId = data.productId
            this.stock = data.stock
            // console.log(this.productCurrency)
            this.dialogNewProduct2 = true
        },
        saveProductUpdate() {
            if (!this.productName) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Updating Product Failed',
                    text: 'Please enter a valid Product Name.'
                })
            } else if (!this.productSize) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Updating Product Failed',
                    text: 'Please enter a valid Product package size.'
                })
            } else if (isNaN(this.productSize)) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Updating Product Failed',
                    text: 'Please enter a valid Product size (it has to be a number)'
                })
            } else if (isNaN(this.stock)) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Updating Product Failed',
                    text: 'Please enter a valid Product stock (it has to be a number)'
                })
            } else {
                //console.log(this.productDetails)
                this.dialogloader = true
                this.productDetails.productCurrency = this.productCurrency
                this.productDetails.productName = this.productName
                this.productDetails.productSize = this.productSize
                this.productDetails.stock = this.stock
                this.productDetails.productId = this.productId
                this.productDetails.createdBy = this.$session.get('user').userId
                // console.log(this.editedUser)
                this.axios.post(this.nodeApi + '/updateProduct', this.productDetails).then((response) => {
                    // console.log(response.data)
                    if (response.data.affectedRows > 0) {
                        this.getProducts()
                        this.dialogNewProduct2 = false
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'success',
                            title: 'Product Updated',
                            text: 'You have successfully updated a product'
                        })
                    } else {
                        this.$swal.fire({
                            type: 'error',
                            title: 'Updating Product Failed',
                            text: response.data.originalError.info.message
                        })
                    }

                }).catch(err => {
                    this.$swal.fire({
                        type: 'error',
                        title: 'Updating Product Failed',
                        text: err
                    })
                })

            }
        },
        saveProduct() {

            if (!this.productDetails.productName) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Product Failed',
                    text: 'Please enter a valid Product Name.'
                })
            } else if (!this.productDetails.productSize) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Product Failed',
                    text: 'Please enter a valid Product package size.'
                })
            } else if (isNaN(this.productDetails.productSize)) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Product Failed',
                    text: 'Please enter a valid Product size (it has to be a number)'
                })
            } else if (isNaN(this.productDetails.stock)) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Product Failed',
                    text: 'Please enter a valid Product stock (it has to be a number)'
                })
            } else {
                //console.log(this.productDetails)
                this.dialogloader = true
                this.productDetails.createdBy = this.$session.get('user').userId
                // console.log(this.editedUser)
                this.axios.post(this.nodeApi + '/addProduct', this.productDetails).then((response) => {
                    // console.log(response.data)
                    if (response.data.affectedRows > 0) {
                        this.getProducts()
                        this.dialogNewProduct = false
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'success',
                            title: 'Product Added',
                            text: 'You have successfully added a new product'
                        })
                    } else {
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'error',
                            title: 'Adding Product Failed',
                            text: response.data.originalError.info.message
                        })
                    }

                }).catch(err => {
                    this.dialogloader = false
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding Supplier Failed',
                        text: err
                    })
                })

            }
        },
        addItem() {
            this.lineItems.push({
                productId: '1',
                productName: 'Click to Edit',
                quantity: 1,
                price: 0,
                linetotal: 0,
            })
        },
        clearForm() {
            this.salesOrder = {
                customerId: '',
                docDate: '',
                docCurrency: '1',
                description: '',
                productId: 0,
                productSize: 0,
                quantity: 0,
                itemData: [],
                docTotal: 0
            }
            this.lineItems = []

            const today = new Date();
            const date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
            this.salesOrder.docDate = date
        },
        closeDelete() {
            this.dialogDelete = false
            this.dialogDelete1 = false
            this.$nextTick(() => {

                this.editedIndex = -1
            })
        },
        deleteItem(item) {
            this.editedIndex = this.lineItems.indexOf(item)

            this.dialogDelete = true
        },

        deleteItemConfirm() {
            this.lineItems.splice(this.editedIndex, 1)
            this.salesOrder.docTotal = 0
            this.lineItems.forEach(element => {
                this.salesOrder.docTotal += element.linetotal
            })
            this.closeDelete()
        },
        deleteItem1(item) {
            this.editedIndex = this.salesOrdersData.indexOf(item)
            this.salesOrderId = item.salesOrderId
            this.dialogDelete1 = true
        },

        deleteItemConfirm1() {
            this.salesOrdersData.splice(this.editedIndex, 1)
            this.axios.post(this.nodeApi + '/removeSalesList', {
                salesOrderId: this.salesOrderId
            }).then((response) => {
                // console.log(response.data)
                if (response.data.affectedRows > 0) {
                    this.$swal.fire({
                        type: 'success',
                        title: 'Sales Deleted successfully',
                        text: 'You have successfully added a new sales order'
                    })
                    this.closeDelete()

                } else {

                    this.$swal.fire({
                        type: 'error',
                        title: 'Sales Deleting Failed',
                        text: response.data.originalError.info.message
                    })
                }

            }).catch(err => {

                this.$swal.fire({
                    type: 'error',
                    title: 'Item Deleting Failed',
                    text: err
                })
            })
        },
        getAllOrders() {
            this.axios.get(this.nodeApi + '/getAllSalesOrders').then((response) => {
                this.salesOrdersData = response.data

            }).catch(err => {
                console.log(err)
            })
        },
        getProducts() {
            this.axios.get(this.nodeApi + '/getAllProducts').then((response) => {
                this.products = response.data
                this.itemProducts = []
                this.products.forEach(element => {
                    if (element.productCurrency == this.docCurrency) {
                        this.itemProducts.push(element.productName)
                    }

                })
            }).catch(err => {
                console.log(err)
            })

        },
        initialize() {
            this.axios.get(this.nodeApi + '/getAllCustomers').then((response) => {
                this.customers = response.data
            }).catch(err => {
                console.log(err)
            })

        },
        salesToday() {
            this.axios.get(this.nodeApi + '/getAllSalesOrdersToday').then((response) => {

                this.salesOrdersData = response.data
            }).catch(err => {
                console.log(err)
            })

        },
        salesSpecific() {
            this.axios.post(this.nodeApi + '/getAllSalesOrdersSpecific', {
                startDate: this.startDate,
                endDate: this.endDate
            }).then((response) => {

                this.salesOrdersData = response.data
                this.dialogSpecific = false

            }).catch(err => {
                console.log(err)
            })

        },

        getDrivers() {
            this.axios.get(this.nodeApi + '/getAllDrivers').then((response) => {
                this.drivers = response.data
            }).catch(err => {
                console.log(err)
            })

        },
        save(data) {
            // console.log(data)
            this.salesOrder.quantity = 0
            this.salesOrder.docTotal = 0
            const result = this.products.find(({
                productName
            }) => productName === data.productName);

            let index = this.lineItems.indexOf(data)
            // console.log(result.stock)
            this.selectedProductStock = result.stock
            if (data.productName == result.productName) {

                this.lineItems[index].productId = result.productId
                this.lineItems[index].price = data.price
                this.lineItems[index].productSize = result.productSize

                this.lineItems[index].linetotal = (data.price * data.quantity)
                this.lineItems.forEach(element => {
                    this.salesOrder.docTotal += element.linetotal
                })
                this.lineItems.forEach(element => {
                    this.salesOrder.quantity += element.quantity
                })

                this.lineItems.forEach(element => {
                    this.salesOrder.productId = element.productId
                })

                this.lineItems.forEach(element => {
                    this.salesOrder.productSize = element.productSize
                })

            } else {
                console.log('Data Mismatch')
            }

            this.snack = true
            this.snackColor = 'success'
            this.snackText = 'Data saved'
        },
        cancel() {
            this.snack = true
            this.snackColor = 'error'
            this.snackText = 'Canceled'
        },
        open() {
            this.snack = true
            this.snackColor = 'info'
            this.snackText = 'Dialog opened'
        },
        close() {
            console.log('Dialog closed')
        },

        saveSalesOrder() {
            if (this.lineItems.length > 1) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Sales Order Failed',
                    text: 'Please only enter one line sales item per order.'
                })
            } else if (this.salesOrder.quantity > this.selectedProductStock) {
                this.$swal.fire({
                    type: 'error',
                    title: 'Adding Sales Order Failed',
                    text: 'Sorry you do not have enough stock for this order.'
                })
            } else {
                this.lineItemsData = this.lineItems
                this.dialogloader = true
                this.salesOrder.itemData = JSON.stringify(this.lineItems)
                this.salesOrder.docCurrency = this.docCurrency
                this.salesOrder.createdBy = this.$session.get('user').userId
                // console.log(this.salesOrder)

                this.axios.post(this.nodeApi + '/addSalesOrder', this.salesOrder).then((response) => {
                    // console.log(response.data)
                    if (response.data.length > 0) {
                        this.$swal.fire({
                            type: 'success',
                            title: 'Sales Order Added',
                            text: 'You have successfully added a new sales order'
                        })

                        this.dialogloader = false
                        this.dialogPrint = true
                        this.invoiceNumner = response.data[1].insertId

                        this.description = this.salesOrder.description

                        let customerId = this.salesOrder.customerId
                        console.log(this.lineItems)
                        var result = this.customers.filter(item => {
                            if (item.customerId == customerId) {
                                return true
                            }
                        })

                        this.customerDetails = result
                        this.customerAddress = this.customerDetails[0].customerAddress
                        this.customerName = this.customerDetails[0].customerName
                        this.customerEmail = this.customerDetails[0].customerEmail
                        this.invoiceTotal = this.salesOrder.docTotal

                        // console.log(this.customerDetails)

                        var d = new Date(Date.now()),
                            month = '' + (d.getMonth() + 1),
                            day = '' + d.getDate(),
                            year = d.getFullYear();

                        this.invoiceDate = `${day}-${month}-${year}`
                        this.clearForm()
                        this.getAllOrders()
                        this.getProducts()
                    } else {
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'error',
                            title: 'Adding Sales Order Failed',
                            text: response.data.originalError.info.message
                        })
                    }

                }).catch(err => {
                    this.dialogloader = false
                    this.$swal.fire({
                        type: 'error',
                        title: 'Adding Sales Order Failed',
                        text: err
                    })
                })

            }
        }

    },
}
</script>

