<template>
<div>
    <v-app-bar color="#003263" dark src="../assets/images/vbanner5.jpg">
        <template v-slot:img="{ props }">
            <v-img v-bind="props" gradient="to top right, rgb(1,48,98,.9), rgb(22,61,99,.8)"></v-img>
        </template>

        <div>
            <img src="../assets/images/ghub_white_.png" style="margin-top:50px;" width="150" class="bounceIn">
        </div>

        <v-spacer></v-spacer>
        <v-toolbar-title>Dashboard</v-toolbar-title>
        <v-btn-toggle v-model="cardDateData" dark dense class="ml-5" color="grey" group>
            <v-btn value="1">
                This Month
            </v-btn>

            <v-btn value="2">
                Over-All
            </v-btn>
        </v-btn-toggle>
        <template v-slot:extension>

        </template>

    </v-app-bar>
    <div style="background-color:#EBEFF3;">
        <v-layout row wrap style="display: flex;justify-content:flex-end;">

            <v-flex xs12 md2 pa-2 pl-5 v-for="(stack, index)  in cardData" :key="index">
                <div>
                    <NMBCard v-bind:value="stack.value" v-bind:name="stack.name" />
                </div>
            </v-flex>
        </v-layout>
        <v-layout row wrap>
            <v-flex xs12 md12 pa-2 pl-5 pr-5>
                <TendChart v-bind:tonnage="tonnageData" v-bind:income="incomeT" v-bind:created="createdData" v-bind:expense="expensesT" />
            </v-flex>
        </v-layout>

        <v-layout row wrap>
            <v-flex xs12 md4 pa-2 pl-5 pb-5>
                <Unresolved v-bind:chartData="chartData" v-bind:recieved="recievedData" v-bind:resolved="resolvedData" />
            </v-flex>
            <v-flex xs12 md4 pa-2>
                <Leaderboard v-bind:chartData="chartData2" v-bind:recieved="recievedData" v-bind:resolved="resolvedData" />
            </v-flex>
            <v-flex xs12 md4 pa-2 pr-5>
                <CustomerSet v-bind:chartData="chartData1" v-bind:recieved="recievedData" v-bind:resolved="resolvedData" />
            </v-flex>
        </v-layout>
    </div>

</div>
</template>

<script>
import NMBCard from '../cards/nmbcard';
import TendChart from '../components/trendchart';
import Unresolved from '../components/unresolvedcard';
import Leaderboard from '../components/leaderboard';
import CustomerSet from '../components/customersat';
export default {
    components: {
        NMBCard,
        TendChart,
        Unresolved,
        Leaderboard,
        CustomerSet
    },
    data() {
        return {
            cardDateData: '1',
            nodeApi: '',
            cardData: [],
            chartData: [],
            chartData1: [],
            chartData2: [],
            recievedData: [],
            resolvedData: [],
            sales: [],
            created: [],
            tonnage: [],
            customers: [],
            drivers: [],
            income: [],
            expenses: [],
            incomeT: [],
            expensesT: [],
            profit: [],
            suppliers: [],
            tonnageData: '',
            createdData: '',
            expensesData: '',
            graphChartDataA: [],
            graphChartDataB: [],
            graphAA: [],
            graphBB: [],
            graphData: [],

        }
    },
    beforeMount() {
        this.getDashboardDataMonth()
        console.log(this.cardDateData)
    },
    mounted() {

    },
    watch: {
        cardDateData(val) {
            if (val) {
                if (val == 1) {
                    this.getDashboardDataMonth()
                } else {
                    this.getDashboardData()
                }
                // console.log(val)
            }

        }
    },
    methods: {
        async getDashboardData() {
            await this.axios.all([
                    this.axios.get(this.nodeApi + '/getTotalSales'),
                    this.axios.get(this.nodeApi + '/getTotalCreated'),
                    this.axios.get(this.nodeApi + '/getTotalTonnage'),
                    this.axios.get(this.nodeApi + '/getTotalCustomerSales'),
                    this.axios.get(this.nodeApi + '/getTotalDriverSales'),
                    this.axios.get(this.nodeApi + '/getTotalIncome'),
                    this.axios.get(this.nodeApi + '/getTotalExpenses'),
                    this.axios.get(this.nodeApi + '/getTotalNetProfit'),
                    this.axios.get(this.nodeApi + '/getTotalSuppliersIn'),
                    this.axios.get(this.nodeApi + '/getTotalIncomeToday'),
                    this.axios.get(this.nodeApi + '/getTotalExpensesToday'),

                ])
                .then(this.axios.spread((totalSales, createdSales, tonnage, customerSales, driverSales, totalIncome, totalExpenses, netProfit, suppliers, incomeToday, expensesToday) => {
                    this.sales = totalSales.data
                    this.created = createdSales.data
                    this.tonnage = tonnage.data
                    this.customers = customerSales.data
                    this.drivers = driverSales.data
                    this.income = totalIncome.data
                    this.expenses = totalExpenses.data
                    this.profit = netProfit.data
                    this.suppliers = suppliers.data
                    this.incomeT = incomeToday.data
                    this.expensesT = expensesToday.data

                }))
                .catch(error => console.log(error));

            this.chartData = this.customers
            this.chartData1 = this.drivers
            this.chartData2 = this.suppliers
            this.tonnageData = this.tonnage[0].tonnage
            this.createdData = this.created[0].sales

            this.cardData = [{
                    name: 'Total Sales',
                    value: this.sales
                },

                {
                    name: 'Total Income',
                    value: this.income
                },
                {
                    name: 'Expenses',
                    value: this.expenses
                },
                {
                    name: 'Net Profit',
                    value: this.profit
                }
            ]
            this.dialogloader = false

        },
        async getDashboardDataMonth() {
            await this.axios.all([
                    this.axios.get(this.nodeApi + '/getTotalSalesMonth'),
                    this.axios.get(this.nodeApi + '/getTotalCreatedMonth'),
                    this.axios.get(this.nodeApi + '/getTotalTonnageMonth'),
                    this.axios.get(this.nodeApi + '/getTotalCustomerSalesMonth'),
                    this.axios.get(this.nodeApi + '/getTotalDriverSalesMonth'),
                    this.axios.get(this.nodeApi + '/getTotalIncomeMonth'),
                    this.axios.get(this.nodeApi + '/getTotalExpensesMonth'),
                    this.axios.get(this.nodeApi + '/getTotalNetProfitMonth'),
                    this.axios.get(this.nodeApi + '/getTotalSuppliersInMonth'),
                    this.axios.get(this.nodeApi + '/getTotalIncomeToday'),
                    this.axios.get(this.nodeApi + '/getTotalExpensesToday'),

                ])
                .then(this.axios.spread((totalSales, createdSales, tonnage, customerSales, driverSales, totalIncome, totalExpenses, netProfit, suppliers, incomeToday, expensesToday) => {
                    this.sales = totalSales.data
                    this.created = createdSales.data
                    this.tonnage = tonnage.data
                    this.customers = customerSales.data
                    this.drivers = driverSales.data
                    this.income = totalIncome.data
                    this.expenses = totalExpenses.data
                    this.profit = netProfit.data
                    this.suppliers = suppliers.data
                    this.incomeT = incomeToday.data
                    this.expensesT = expensesToday.data

                }))
                .catch(error => console.log(error));

            this.chartData = this.customers
            this.chartData1 = this.drivers
            this.chartData2 = this.suppliers
            this.tonnageData = this.tonnage[0].tonnage
            this.createdData = this.created[0].sales

            this.cardData = [{
                    name: 'Total Sales',
                    value: this.sales
                },

                {
                    name: 'Total Income',
                    value: this.income
                },
                {
                    name: 'Expenses',
                    value: this.expenses
                },
                {
                    name: 'Net Profit',
                    value: this.profit
                }
            ]
            this.dialogloader = false

        }
    }

}
</script>

