<template>
<div class="main">

    <div v-bind:style="logoStyle">
        <div v-bind:style="loginPage">
            <img style="margin-bottom: 25px;" src="../assets/images/ghub_.png" width="300" class="bounceIn">
        </div>

        <div :style="form">
            <form>
                <h4 v-if="leftDays <= 0" style="color:red">Sorry your licence has expired</h4>
                <h4 v-if="leftDays <= 30 && leftDays >=1" style="color:red">Licence expiring in {{leftDays}} days</h4>
                <br>
                <input :style="formInput" v-model="userName" type="text" placeholder="User Email" />
                <input :style="formInput" v-model="password" type="password" placeholder="Password" />
                <button v-if="!active" disabled :style="loginButton1" @click.prevent="login">login</button>
                <button v-if="active" :style="loginButton" @click.prevent="login">login</button>
            </form>

        </div>

    </div>

    <v-dialog v-model="dialogloader" hide-overlay persistent width="300">
        <v-card color="#003263" dark>
            <v-card-text>
                <br>
                <h3>Please stand by</h3>
                <br>
                <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
                <br>
            </v-card-text>
        </v-card>
    </v-dialog>
</div>
</template>

<script>
export default {
    data() {
        return {
            nodeApi: '',
            userName: '',
            password: '',
            active: false,
            leftDays: 0,
            dialogloader: false,
            imageLogo: '../assets/logo.png',
            logoStyle: {
                'text-align': 'center',
            },
            loginPage: {
                width: '360px',
                padding: '10% 0 0',
                margin: 'auto',
            },
            form: {
                position: 'relative',
                'z-index': 1,
                background: '#FFFFFF',
                'max-width': '360px',
                margin: '0 auto 100px',
                padding: '45px',
                'text-align': 'center',
                'box-shadow': '0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24)'
            },
            formInput: {
                'font-family': '"Roboto", sans-serif',
                outline: 0,
                background: '#f2f2f2',
                width: '100%',
                border: 0,
                margin: '0 0 15px',
                padding: '15px',
                'box-sizing': 'border-box',
                'font-size': '14px'
            },
            loginButton: {
                'font-family': '"Roboto", sans-serif',
                'text-transform': 'uppercase',
                outline: 0,
                background: '#003263',
                width: '100%',
                border: 0,
                padding: '15px',
                color: '#FFFFFF',
                'font-size': '14px',
                '-webkit-transition': 'all 0.3 ease',
                transition: 'all 0.3 ease',
                cursor: 'pointer'
            },
            loginButton1: {
                'font-family': '"Roboto", sans-serif',
                'text-transform': 'uppercase',
                outline: 0,
                background: 'grey',
                width: '100%',
                border: 0,
                padding: '15px',
                color: '#FFFFFF',
                'font-size': '14px',
                '-webkit-transition': 'all 0.3 ease',
                transition: 'all 0.3 ease',
                cursor: 'pointer'
            },
        }
    },
    mounted() {
        this.check()
    },
    methods: {
        async check() {

            await this.axios.all([
                    this.axios.get(this.nodeApi + '/getAdmin')

                ])
                .then(this.axios.spread((adminData) => {
                    if (adminData.data[0].leftDays > 0) {
                        this.active = true
                        this.leftDays = adminData.data[0].leftDays
                    } else {
                        this.active = false
                    }

                }))
                .catch(error => console.log(error));

        },

        login() {
            this.dialogloader = true
            if (this.userName && this.password) {
                this.axios.post(this.nodeApi + '/getUser', {
                    emailAddress: this.userName,
                    userPassword: this.password
                }).then((response) => {
                    //console.log(response.data.recordset)
                    if (response.data.length == 1) {
                        this.dialogloader = false
                        this.$session.start()
                        this.$session.set('user', response.data[0])
                        this.$router.push({
                            name: 'Home'
                        }).catch(() => {});
                        console.log(this.$session.get('user'))
                    } else {
                        this.dialogloader = false
                        this.$swal.fire({
                            type: 'error',
                            title: 'Login Failure',
                            text: 'Invalid username and password.'
                        })
                    }
                }).catch(err => {
                    this.dialogloader = false
                    this.$swal.fire({
                        type: 'error',
                        title: 'Logout Failed',
                        text: err
                    })
                })

            } else {
                this.dialogloader = false
                this.$swal.fire({
                    type: 'error',
                    title: 'Login Failure',
                    text: 'Please enter a valid username and password.'
                })
            }

        }
    }

}
</script>

<style scoped>
.main {
    background-image: url('../assets/images/bg2.jpg');
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100% 100%;
}
</style>
