<script>
import Dashboard from '@/pages/dashboard'
import Sales from '@/pages/sales'
import Accounts from '@/pages/accounts'
import Logistics from '@/pages/logistics'
import Customers from '@/pages/customers'
import Suppliers from '@/pages/suppliers'
import Users from '@/pages/users'
import Silo from '@/pages/silo'
import Stocks from '@/pages/stocks'

export default {
    components: {

        'dashboard': Dashboard,
        'sales': Sales,
        'accounts': Accounts,
        'logistics': Logistics,
        'customers': Customers,
        'suppliers': Suppliers,
        'users': Users,
        'silo': Silo,
        'stocks': Stocks
    },
    data() {
        return {
            nodeApi: '',
            userName: '',
            userType: null,
            page: '',
            drawer: false,
            dialogloader: false,
        }
    },

    mounted() {
        if (this.$session.exists()) {

            this.userName = this.$session.get('user').firstName
            this.userType = this.$session.get('user').userTypes

            const Toast = this.$swal.mixin({
                toast: true,
                position: 'top-end',
                showConfirmButton: false,
                timer: 3000
            });

            Toast.fire({
                type: 'success',
                title: 'Signed in successfully'
            })
            this.updateAdmin()
        } else {
            this.$router.push({
                name: 'Login'
            }).catch(() => {});

        }
        if (this.userType == 2) {
            this.page = 'sales'
        } else if (this.userType == 3) {
            this.page = 'accounts'
        }else if (this.userType == 5) {
            this.page = 'silo'
        } else {
            this.page = 'dashboard'
        }

    },
    methods: {
        updateAdmin() {
            this.axios.get(this.nodeApi + '/updateAdmin').then((response) => {
                console.log(response.data)

            }).catch(err => {
                console.log(err)
            })
        },
        Headering(data) {
            if (data == 'users') {
                return 'User Admin Page'
            }
            if (data == 'dashboard') {
                return 'Dashboard Page'
            }
            if (data == 'tickets') {
                return 'Tickets Page'
            }
            if (data == 'settings') {
                return 'Settings Page'
            }
            if (data == 'crm') {
                return 'Customer Relations Management Page'
            }
            if (data == 'reports') {
                return 'Reports Page'
            }
        },
        logout() {
            this.$session.destroy()
            this.$router.push({
                name: 'Login'
            }).catch(() => {});

        },
        selectMenu(data) {
            this.page = ''
            this.page = data
            //console.log(this.page)
        }
    },
}
</script>
